import React, { useContext } from 'react'
import { MyContext } from '../../Context'
import{ Button } from 'antd'
import { Link } from 'react-router-dom'
import useForm from '../../hooks/useForm'

const Profile = props => {
  const { state } = useContext(MyContext)
  const [form, uploadPhoto] = useForm()

 if(!state.user)return <p>Loading...</p>
 return(
  <div className="profile">
    <div className="profileContainer">
      <div className="imgAndName">
        <img src={state.user.image} alt="profile"/>
        <input type="file" name="image" onChange={uploadPhoto} required/>
        {form.image && <img src={form.image} alt="photo_url" width="100"/>}
        <h1>{state.user.username}</h1>
        <MyContext.Consumer>
        {({state}) => (
          //console.log(state)
          (state.user.role === 'ADMIN' ? <div>
            <p>ADMIN</p>
            <Button>
              <Link to="/createBeca">
                CREAR BECA
              </Link>
            </Button>
            <Button>
              <Link to="/createPremio">
              CREAR PREMIO O CONCURSO
              </Link>
            </Button>
            </div> : '')
        )}
        </MyContext.Consumer>
      </div>
      <div className="interests">
        <p>Áreas de interés</p>
      </div>
    </div>
  </div>
)
}

export default Profile