import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'
import { MyContext } from '../../Context'
import { Button } from 'antd'
import useForm from '../../hooks/useForm'

const DetailBeca = (props) => {
  const [beca, setBeca] = useState({})
  const {changePlace} = useContext(MyContext)
  const [form, handleInputs, uploadPhoto] = useForm()
  const {id} = props.match.params
  const isProduction = process.env.NODE_ENV === "production"
  const baseURL = isProduction ? 'https://becasmx.herokuapp.com' : 'http://localhost:3000'

  useEffect(() => {
    axios.get(`${baseURL}/api/beca/`+id)
    .then(({data}) => {
        setBeca(prevState => {
          return {prevState, ...data.beca}
        })
    })
    .catch(err => console.log(err))
    changePlace('Becas')
  }, [changePlace, id, baseURL])

  const deleteBeca = () => {
  axios.delete(`${baseURL}/api/beca/${id}`)
  .then(({data}) => {
    props.history.push('/becas')
  })
  .catch(err => console.log(err))
  }

  const updateBeca = () => {
    axios.put(`${baseURL}/api/beca/${id}`, form)
      .then(({ data }) => {
        setBeca(prevState => {
          return {
            ...prevState,
            ...data.beca
          }
        })
      })
      .catch(err => {
        console.log(err)
      })
  }

  return (
    <div className="detailContainer">
      <h1>{beca.title}</h1>
      <MyContext.Consumer>
        {({state})=>{
          console.log(beca)
          if(!state.user) return <p>....Loading</p>
          return (state.user.role==='ADMIN') ? 
          <div className="">
            <Button onClick={deleteBeca}>Borrar</Button>
            <Link to="/editBeca" params={{beca:{beca}, form:{form}, uploadPhoto:{uploadPhoto}, handleInputs:{handleInputs}, updateBeca:{updateBeca}}}>EDITAR</Link>    
          </div> :
        ''}}
      </MyContext.Consumer>
      <div className="imgAndDescription">
        <img src={beca.image} alt="img"/>
        <div className="description">
        <h3>DESCRIPCIÓN</h3>
        <p>{beca.description}</p>
        <h3>FECHA LÍMITE PARA APLICAR</h3>
        <p>{beca.endDate}</p>
        <a href={beca.link} target="blank"><Button>Convocatoria</Button></a>
        </div>
      </div>
    </div>
  )}

export default DetailBeca
