import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'
import { MyContext } from '../../Context'
import { Button } from 'antd'
import useForm from '../../hooks/useForm'

const DetailPremio = (props) => {
  const [pc, setPC] = useState({})
  const {changePlace} = useContext(MyContext)
  const [form, handleInputs] = useForm()
  const {id} = props.match.params
  const isProduction = process.env.NODE_ENV === "production"
  const baseURL = isProduction ? 'https://becasmx.herokuapp.com' : 'http://localhost:3000'

  useEffect(() => {
    axios.get(`${baseURL}/api/pc/${id}`)
    .then(({data}) => {
        setPC(prevState => {
          return {prevState, ...data.pc}
        })
    })
    .catch(err => console.log(err))
    changePlace('Premios')
  }, [changePlace, id, baseURL])

  const deletePremio = () => {
    axios.delete(`${baseURL}/api/pc/${id}`)
    .then(({data}) => {
      props.history.push('/pc')
    })
    .catch(err => console.log(err))
    }
  
    const updatePremio = () => {
      axios.put(`${baseURL}/api/pc/${id}`, form)
        .then(({ data }) => {
          setPC(prevState => {
            return {
              ...prevState,
              ...data.beca
            }
          })
        })
        .catch(err => {
          console.log(err)
        })
    }

  return (
    <div className="detailContainer">
      <h1>{pc.title}</h1>
      <MyContext.Consumer>
        {({state})=>{
          if(!state.user) return <p>....Loading</p>
          return (state.user.role==='ADMIN') ? 
          <div className="">
            <Button onClick={deletePremio}>Borrar</Button>
            <Link to="/editPremio" params={{pc:{pc}, handleInputs:{handleInputs}, updateBeca:{updatePremio}}}>EDITAR</Link>    
          </div> :
        ''}}
      </MyContext.Consumer>
      <div className="imgAndDescription">
        <img src={pc.image} alt="img"/>
        <div className="description">
        <h3>DESCRIPCIÓN</h3>
        <p>{pc.description}</p>
        <h3>PREMIO</h3>
        <p>${pc.prize}.00 {pc.prizeCurrency}</p>
        <h3>FECHA LÍMITE PARA APLICAR</h3>
        <p>{pc.endDate}</p>
        <a href={pc.link} target="blank"><Button>Convocatoria</Button></a>
        </div>
      </div>
    </div>
  )
}

export default DetailPremio
